import React from 'react';


const Tabel = ({Review,handeldelt}) => {

   
     const {review,name,_id} = Review
   

    return (

        <tr class="bg-gray-100">
            <td class="w-1/3 text-left py-3 px-4">{name}</td>
            <td class="w-1/3 text-left py-3 px-4">{review.slice(0,20)}...</td>
            <td class="text-left py-3 px-4"><button onClick={()=>handeldelt(_id)}> Delt</button></td>
            <td class="text-left py-3 px-4"><button> Update</button></td>
        </tr>

    );
};

export default Tabel;