import React, { useContext, useEffect, useState } from 'react';
import { Authcontex } from '../../AllContex/Usercontex';
import Tabel from './Tabel';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
const Myreviews = () => {

    const notify = () => toast("Product Deleted Successfully")
    const { user } = useContext(Authcontex)

    const [reviews, setreviews] = useState([])
    useEffect(() => {
        fetch(`https://youtuber-server-ten.vercel.app/userreviews?email=${user?.email}`)
            .then(res => res.json())
            .then(data => setreviews(data))
    }, [user?.email])

    const handeldelt = (id) => {
        fetch(`https://youtuber-server-ten.vercel.app/reviews/${id}`, {
            method: 'DELETE'
        })
            .then(res => res.json())
            .then(result => {
                if (result) {
                    notify()
                    console.log(result);
                }
                const newreview = reviews.filter(review => review._id !== id)
                setreviews(newreview)

            })
    }
    return (
        <div>
            <div class="md:px-32 py-8 w-full">
                <div class="shadow overflow-hidden rounded border-b border-gray-200">
                    <table class="min-w-full bg-white">
                        <thead class="bg-gray-800 text-white">
                            <tr>
                                <th class="w-1/3 text-left py-3 px-4 uppercase font-semibold text-sm">Product Name</th>
                                <th class="w-1/3 text-left py-3 px-4 uppercase font-semibold text-sm"> Review</th>
                                <th class="text-left py-3 px-4 uppercase font-semibold text-sm">DElt</th>
                                <th class="text-left py-3 px-4 uppercase font-semibold text-sm">Update</th>
                            </tr>
                        </thead>
                        <tbody class="text-gray-700">
                            {
                                reviews.map(Review => <Tabel Review={Review} handeldelt={handeldelt}></Tabel>)
                            }
                        </tbody>
                    </table>
                </div>
            </div>
            <ToastContainer />
        </div>
    );
};

export default Myreviews;